.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
 html, body, #root {
  margin-right: 4px;
  background-color: lavender;
}
.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}



.footer {
  height: 30vh;
  background-color: #333;
  color: #ffffff;
  text-align: center;
 margin-top: 60vh;
  font-size: 2vh;
  bottom: 0;
}

.footer-container {
  max-width: 2000px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 5rem;
}

.footer-section {
  margin: 15px 0;
  flex: 1;
}

.footer-section a {
  color: #bbb;
  text-decoration: none;
  display: block;
}

.footer-section a:hover {
  color: white;
}
